'use server'

import { createClient } from "@/lib/supabase/server"
import { revalidatePath } from "next/cache"
import { redirect } from "next/navigation"

export async function logout() {
    const supabase = createClient()
    await supabase.auth.signOut()
    redirect('/login')
}

export async function createCustomer(formData: FormData) {
    const supabase = createClient();
    const { data: { user } } = await supabase.auth.getUser();

    if (!user) {
        return { error: 'You must be logged in to create a customer.' };
    }

    const customerData = {
        name: formData.get('name') as string,
        nic: formData.get('nic') as string,
        contact: formData.get('contact') as string,
        order_history: formData.get('orderHistory') as string,
        preferences: formData.get('preferences') as string,
        user_id: user.id
    };

    const { data: newCustomer, error } = await supabase
        .from('customers')
        .insert(customerData)
        .select()
        .single();
    
    if (error) {
        console.error('Error creating customer:', error);
        return { error: error.message };
    }

    revalidatePath('/dashboard');
    redirect(`/dashboard/customer/${newCustomer.id}`);
}

export async function updateCustomer(customerId: string, formData: FormData) {
    const supabase = createClient();

    const customerData = {
        name: formData.get('name') as string,
        nic: formData.get('nic') as string,
        contact: formData.get('contact') as string,
        order_history: formData.get('orderHistory') as string,
        preferences: formData.get('preferences') as string,
    };

    const { error } = await supabase
        .from('customers')
        .update(customerData)
        .eq('id', customerId);

    if (error) {
        console.error('Error updating customer:', error);
        return { error: error.message };
    }

    revalidatePath(`/dashboard/customer/${customerId}`);
    revalidatePath('/dashboard');
    return { success: true };
}